<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom:20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、登录名"
                      @keyup.native.enter="conditionQuery"
                      v-model="currencyTrans.username">
            </el-input>
            <span>商品类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.goodsId">
                <el-option v-for="status in coinList" :key="status.baseSeq" :label="status.chineseName"
                           :value="status.baseSeq">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="conditionQuery" size="small" style="margin-left: 30px">查询
            </el-button>
            <el-button type="primary" @click="addpostexpress" size="small" style="margin-left: 100px">新增快递机构
            </el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label="操作"
                        width="200">
                    <template scope="scope" class="filter-operation">
                        <ul style="list-style: none;padding: 0px;height: 30px;margin: 0px">
                            <li style="float:right;width: 60px">
                                <el-button
                                        type="text"
                                        @click="cancelgoods(scope.row.id)">取消提现
                                </el-button>
                            </li>
                            <li style="float:right;width: 100px" v-if="scope.row.status===6">
                                <el-button
                                        type="text"
                                        @click="passApproval(scope.row)">审核
                                </el-button>
                            </li>

                            <li style="float:right;width: 60px"
                                v-if="scope.row.status===6">
                                <el-button
                                        type="text"
                                        @click="lockgoods(scope.row.id,1,'取消锁定')">取消锁定
                                </el-button>
                            </li>
                            <li style="float:right;width:100px"
                                v-else-if="scope.row.status!==6">
                                <el-button
                                        type="text"
                                        @click="lockgoods(scope.row.id,6,'锁定')">锁定
                                </el-button>
                            </li>
                            <li style="float:right;width: 100px"
                                v-if="scope.row.status===6">
                                <el-button
                                        type="text"
                                        @click="issuegoods(scope.row.id,2)">移入问题列表
                                </el-button>
                            </li>
                        </ul>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="currencyTrans.page.pageNum"
                               :page-sizes="[15,30,45,60]" :page-size="currencyTrans.page.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
        <el-dialog v-loading="listLoading" element-loading-text="给我一点时间" title="确认提示" :visible.sync="dialogFormVisible">
            <el-form :model="applygoods" :rules="appgoods" ref="applygoods">
                <el-form-item label="快递机构：" :label-width="formLabelWidth" prop="expressAgentId">
                    <el-select class="filter-item" v-model="applygoods.expressAgentId">
                        <el-option v-for="item in deliverylist"
                                   :key="item.id"
                                   :label="item.expressName"
                                   :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="快递单号" :label-width="formLabelWidth" prop="expressNo">
                    <el-input placeholder="请输入快递单号" v-model="applygoods.expressNo"></el-input>
                </el-form-item>
            </el-form>
            <el-form :model="temp">
                <el-form-item label="收货人姓名:" :label-width="formLabelWidth">
                    <div>{{temp.realName}}</div>
                </el-form-item>
                <el-form-item label="收货人电话:" :label-width="formLabelWidth">
                    <div>{{temp.mobile}}</div>
                </el-form-item>
                <el-form-item label="地址:" :label-width="formLabelWidth">
                    <div>{{temp.location}}{{temp.deliveryAddress}}</div>
                </el-form-item>
                <el-form-item label="提货:" :label-width="formLabelWidth">
                    <div>{{temp.applyDeliveryCount}}</div>
                </el-form-item>
                <el-form-item label="数量:" :label-width="formLabelWidth">
                    <div>{{temp.goodsName}}</div>
                </el-form-item>
                <el-form-item label="申请时间:" :label-width="formLabelWidth">
                    <div>{{temp.createTime}}</div>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible=false">取 消</el-button>
                <el-button type="primary" @click="reviewgoods">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog element-loading-text="给我一点时间" title="新增快递机构" :visible.sync="xpressVisible">
            <el-form :model="xpress" :rules="xpressgoods" ref="xpress">
                <el-form-item label="快递机构名称" :label-width="formLabelWidth" prop="expressName">
                    <el-input placeholder="请输入快递单号" v-model="xpress.expressName"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="xpressVisible=false">取 消</el-button>
                <el-button type="primary" @click="postexpress">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import fetch from '@/utils/fetch';
    import DCTP from '@/utils/dctp';
    import {characterSize} from '@/utils/validate';
    import {
        goodsquery,
        getexpress,
        reviewgoods,
        lockgoods,
        cancelgoods,
        issuegoods,
        postexpress,
        getgoodslist
    } from '@/api/awaits';
    //启用禁用筛选条件
    const allCoin = {baseSeq: '', chineseName: '全部商品', englishName: 'All'};
    const express = {id: 0, expressName: '请选择快递机构'};
    const goodsone = {id: 0, goodsName: '全部商品'};
    const statuScope = [
        {key: 0, display_name: '全部状态'},
        {key: 1, display_name: '等待审核'},
        {key: 2, display_name: '等待收货'},
        {key: 3, display_name: '成功提现'},
        {key: 4, display_name: '系统撤销'},
        {key: 5, display_name: '用户撤销'},
        {key: 6, display_name: '正在处理'},
    ];

    export default {
        data() {
            let validate = (rule, value, callback) => {
                if (value === '' || value === 0) {
                    callback(new Error('请选择快递'));
                }
                callback();
            };
            let validateNo = (rule, value, callback) => {
                if (characterSize(value) > 40) {
                    callback(new Error('最大40字符'));
                } else if (value === '') {
                    callback(new Error('请输入信息'));
                }
                callback();
            };
            return {
                //搜索需求数据等
                goodsone,
                express,
                allCoin,
                goodsList: [],
                coinList: [],
                dateTime: '',
                tableData: [],
                deliverylist: [],
                xpressVisible: false,
                goodsall: {
                    types: [2]
                },
                xpress: {
                    expressName: '',
                },
                applygoods: {
                    id: '',
                    expressAgentId: 0,
                    expressNo: ''
                },
                formLabelWidth: '120px',
                total: null,
                dialogFormVisible: false,
                lockWithdrawalVo: {
                    id: '',
                    status: ''
                },

                temp: {
                    mobile: '',
                    location: '',
                    realName: '',
                    deliveryAddress: '',
                    applyDeliveryCount: '',
                    createTime: '',
                    goodsName: '',

                },
                currencyTrans: {
                    username: "",
                    goodsId: "",
                    status: 1,
                    page: {
                        pageSize: 15,
                        pageNum: 1
                    }
                },
                msg: '',
                listLoading: true,
                appgoods: {
                    expressAgentId: [{validator: validate, trigger: 'blur'}],
                    expressNo: [{validator: validateNo, trigger: 'blur'}],
                },
                xpressgoods: {
                    expressName: [{validator: validateNo, trigger: 'blur'}],
                },
                tableHeader: [
                    {
                        label: "单号id",
                        prop: "id",
                        width: 45
                    },
                    {
                        label: "会员UID",
                        prop: "userUid",
                        width: 55
                    }, {
                        label: "登录名",
                        prop: "loginType",
                        width: 120,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? row.loginName : (value === 2 ? row.email : '未知');
                        }
                    }, {
                        label: "会员昵称",
                        prop: "nickName",
                        align: 'center',
                        width: 55
                    }, {
                        label: "真实姓名",
                        prop: "realName",
                        align: 'center',
                        width: 55
                    }, {
                        label: "商品类型",
                        prop: "goodsName",
                        align: 'center',
                        width: 65,
                    }, {
                        label: "状态",
                        prop: "status",
                        width: 65,
                        align: 'center',
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(statuScope, value, 'key');
                            return item ? item.display_name : '';
                        }
                    }, {
                        label: "数量",
                        prop: "applyDeliveryCount",
                        width: 45,
                        align: 'center'
                    }, {
                        label: "提货人",
                        prop: "realName",
                        align: 'center',
                        width: 55
                    }, {
                        label: "手机号",
                        prop: "mobile",
                        align: 'center',
                        width: 100
                    }, {
                        label: "所在区域",
                        prop: "location",
                        width: 100
                    }, {
                        label: "收货地址",
                        prop: "deliveryAddress",
                        width: 320
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "最后修改时间",
                        prop: "updateTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }
                ]
            }
        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {


            //获取当前页数
            userPage(val) {
                this.currencyTrans.page.pageNum = val;
                this.getList();
            }
            ,
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.page.pageSize = val;
                this.getList();
            }

            ,
            //条件查询方法
            conditionQuery() {
                this.getList();
            },
            /**
             * 移入问题列表方法
             * @param id
             * @param status
             */
            issuegoods(id, type) {
                issuegoods(id, type).then(response => {
                    if (response.result) {
                        this.msg = "已移入问题列表";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    } else {
                        this.msg = "移入问题列表失败";
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 锁定订单方法
             * @param id
             * @param status
             */
            lockgoods(id, status, msg) {
                lockgoods(id, status).then(response => {
                    if (response.result) {
                        this.msg = '已' + msg + '此单';
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    } else {
                        this.msg = msg + '操作失败';
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 取消提现方法
             * @param id
             */
            cancelgoods(id) {
                cancelgoods(id).then(response => {
                    if (response.result) {
                        this.msg = "已取消提现";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 审核填写信息
             * @param vol
             */
            passApproval(vol) {
                getexpress().then(response => {
                    this.deliverylist = response.data;
                    this.deliverylist.unshift(this.express);
                });
                this.temp = vol;
                this.applygoods.id = vol.id;
                this.dialogFormVisible = true;
            }
            ,
            /**
             * 审核通过方法
             * @param vol
             */
            reviewgoods() {
                let _this = this;
                this.$refs['applygoods'].validate((valid) => {
                    if (valid) {
                        console.log(this.applygoods);
                        reviewgoods(this.applygoods).then(response => {
                            if (response.result) {
                                this.msg = "已提交";
                                this.$message({
                                    type: 'success',
                                    message: this.msg
                                });
                                this.dialogFormVisible = false;
                            } else {
                                this.msg = response.msg;
                                this.$message({
                                    type: 'error',
                                    message: this.msg
                                });

                            }
                            this.getList();
                        });
                    }
                });
            },
            /**
             * 请求列表方法
             */
            getList() {
                this.listLoading = true;
                goodsquery(this.currencyTrans).then(response => {
                    this.tableData = response.data.data;
                    this.total = response.data.page.count;
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                    this.listLoading = false;
                })
            },
            /**
             * 添加快递信息
             */
            addpostexpress() {
                this.xpress.expressName = '',
                    this.xpressVisible = true;
            },
            postexpress() {
                this.$refs['xpress'].validate((valid) => {
                    if (valid) {
                        postexpress(this.xpress).then(response => {
                            if (response.result) {
                                this.xpressVisible = false;
                                this.msg = "已添加";
                                this.$message({
                                    type: 'success',
                                    message: this.msg
                                });
                            } else {
                                this.msg = response.msg;
                                this.$message({
                                    type: 'error',
                                    message: this.msg
                                });

                            }
                            this.getList();
                        });
                    }
                });
            },
            getgoods() {
                getgoodslist(this.goodsall).then(response => {
                    this.coinList = response.data;
                    this.coinList.unshift(this.allCoin);
                })
            }
        },
        created() {
            this.getList();
            this.getgoods();
        }
    }
</script>



